import React from 'react';
import "../css/signup.css";
import { useForm } from "react-hook-form";

const Signup  = () => {
  const {register,handleSubmit,formState:{errors}} = useForm();
  const onSubmit = (data) => {
  console.log(data);
};
    return(
        <div id="signupForm">
          <form onSubmit = {handleSubmit(onSubmit)}>

      <div className="hii">
        <label className="required"> <i className="fa-regular fa-user"></i> Username</label>
        <input type="text" maxLength="15" {...register("username",{
          required: {
            value:true,
            message:'username is required'
          },
          minLength:{
            value:3,
            message:'username should have atleast 3 characters'
          },
          maxLength: {
            value:10,
            message:'username should have 10 characters'
          }
        })} />
      </div>
      <small>{errors.username?.message}</small>

      <div className="hii">
        <label className="required"> <i className="fa-solid fa-key"></i> Password</label>
        <input type="password" maxLength="10" {...register("password",{
           pattern:{
            value:/^(?=.*[a-z])(?=.*[A-Z])(?=.*\d)(?=.*[@$!%*?&])[A-Za-z\d@$!%*?&]{8,}$/,
            message:"Minimum eight characters, at least one uppercase letter, one lowercase letter, one number and one special character"
          }
        })} />
      </div>
      <small>{errors.password?.message}</small>

      <div className="hii">
        <label className="required"> <i className="fa-solid fa-key"></i> Re-Password</label>
        <input type="password" maxLength="10"{...register("Re-password",{
           pattern:{
            value:/^(?=.*[a-z])(?=.*[A-Z])(?=.*\d)(?=.*[@$!%*?&])[A-Za-z\d@$!%*?&]{8,}$/,
            message:"Minimum eight characters, at least one uppercase letter, one lowercase letter, one number and one special character"
          }
        })}/>
      </div>
      <small>{errors.password?.message}</small>

      <div className="hii">
        <button>Signup <i className="fa-solid fa-arrow-right-to-bracket"></i></button>
      </div>
      </form>
    </div>
    )
}
export default Signup;