import React from "react";
import profile from "../assets/imagesicon.jpg";

const Card = (props) =>{
    return(
        <div className="card">
        <img src={profile} alt="images" width="200" height="210" />
        <div className="sara">
          <p>Username : <span>{props.emp.Name}</span></p>
          <p>Id No : <span>{props.emp.EmployeeID}</span></p>
        </div>
        <button onClick={() => props.btnfn()} id="view-btn">{props.btnname}</button>
      </div>
    
    
    )
}
export default Card;    